<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <!-- Page Content -->
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container text-start">
        <div class="my-2">گزارش همایش ها:</div>
      </div>
      <div class="row">
        <div class="col">
          <select class="form-select" v-model="productDetailVideosId">
            <option
              v-for="item in conferencesList"
              :key="item.id"
              :value="item.id"
            >
              {{ item.name }}
            </option>
          </select>
        </div>
        <div class="col text-start">
          <button
            class="btn btn-success"
            :disabled="loading"
            @click="getReport"
          >
            جستجو
          </button>
        </div>
      </div>

      <table class="table">
        <thead>
          <tr>
            <th>ردیف</th>
            <th>نام</th>
            <th>نام و نام خانوادگی</th>
            <th>موبایل</th>
            <th>منبع</th>
            <th>وضعیت</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in report" :key="item.id">
            <td>
              {{ index + 1 }}
            </td>
            <td>
              {{ item.first_name }}
            </td>
            <td>
              {{ item.last_name }}
            </td>
            <td>
              {{ item.email }}
            </td>
            <td>
              {{ item.referrer }}
            </td>
            <td>
              <span v-if="item.already_registerd === 'yes'">
                قبلا ثبت نام بوده
              </span>
              <span v-else class="text-success">ثبت نام جدید</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import TheSidemenu from "../../components/TheSidemenu";
import TheTopmenu from "../../components/TheTopmenu";
import ArefApi from "../../libs/api.js";
import utils from "../../libs/utils.js";

export default {
  name: "ConferenceReport",
  created: function () {
    this.utils = new utils();
    this.arefApi = new ArefApi(this);
    this.getAllConferencesList();
  },
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      conferencesList: [],
      productDetailVideosId: null,
      report: [],
      loading: false,
    };
  },
  methods: {
    getAllConferencesList() {
      this.arefApi
        .getAllConferencesList()
        .then((res) => {
          this.conferencesList = res.data;
          if (this.conferencesList.length) {
            this.productDetailVideosId = this.conferencesList[0].id;
          }
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        });
    },
    getReport() {
      this.loading = true;
      this.arefApi
        .getConferenceReport(this.productDetailVideosId)
        .then((res) => {
          this.report = res.data;
        })
        .catch((err) => {
          this.utils.showErrors(this, err);
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
};
</script>